import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { Affiliation, Company, UserAccount, UserRole } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { UserAccountComponent } from '../../maintenance/user-account/user-account.component';
import { UserRegistrationComponent } from '../user-registration/user-registration.component';

@Component({
  selector: 'app-user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss']
})
export class UserProfileComponent implements OnInit {

  userProfile = new UserAccount();

  constructor(
    private dataStorage: DataStorageService,
    private apiService: ApiService,
    private sweetAlertService: SweetAlertService,
    private router: Router,
    private route: ActivatedRoute,
    private dialog: MatDialog,
   ) {}

  ngOnInit(): void {
    this.userProfile = this.dataStorage.getUserAccount();
  }

  updateAccount(): void {
    const dialogRef = this.dialog.open(UserAccountComponent, {
      width: '80%',
      height: '85%',
      data: {
        isFromProfile: true,
        userAccount: this.userProfile
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed', result);
      if(result){
        this.dataStorage.signOut();
        this.router.navigateByUrl('login');
      }
      
    });
  }

}
